}
@font-face {
  font-family:'beautiful-bloom';
  src:local('beautiful-bloom'),url(/react-portfolio/static/media/BeautifulBloom-Regular.8a5d3dd7.ttf) format('truetype');
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
